
.load-detail {
  background: -webkit-gradient(linear, left top, left bottom, from(#a6a6a6), to(#d6d6d6));
  background: linear-gradient(to bottom, #a6a6a6, #d6d6d6);
  border: 1px solid #626262;
  -webkit-box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
  padding: 5px;
  width: 320px;
}
.load-detail .ldr {
    margin-top: 10px;
}
.load-detail .ldr > div {
      width: 50%;
      -webkit-box-sizing: border-box;
              box-sizing: border-box;
      line-height: 24px;
      height: 26px;
      padding: 0 8px;
      float: left;
}
.load-detail .ldr > div:first-child {
        text-align: right;
}
.load-detail .ldr .ldr-value {
      border: 1px solid #000;
      background-color: #ffffff;
      white-space: nowrap;
      overflow: hidden;
}
.load-detail .ldr .ldr-label {
      white-space: nowrap;
}



