
.overview-item {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 250px;
  border-radius: 10px;
  padding: 10px;
  border: 1px solid #424242;
  -webkit-box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
}
.oi-title {
  text-align: center;
}
.oi-ebtn {
  width: 250px;
  padding: 5px;
  margin: 0 auto;
  margin-top: 12px;
  background-color: #00b050;
  text-align: center;
  border: 1px solid #424242;
  border-radius: 10px;
}
.oi-footer {
  position: absolute;
  bottom: 10px;
}
.oi-footer p {
    margin: 10px 0;
}
.typeimg {
  width: 120px;
  position: absolute;
  bottom: 10px;
  right: 20px;
}
.oi-col {
  padding-top: 10px;
  padding-bottom: 10px;
}



