
.load-status {
  width: 500px;
  position: relative;
}
.load-status .esd {
    background-color: red;
}
.load-status-box {
  background: -webkit-gradient(linear, left top, left bottom, from(#a6a6a6), to(#d6d6d6));
  background: linear-gradient(to bottom, #a6a6a6, #d6d6d6);
  padding: 5px;
  border: 1px solid #4a4a4a;
  -webkit-box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
  width: 320px;
}
.load-status-box > div {
    white-space: nowrap;
    border: 1px solid #000;
    margin-top: 10px;
    background-color: #00b050;
    padding: 2px 4px;
}
.load-status-box > div:last-child {
      margin-bottom: 10px;
}
.flexlinc {
  position: absolute;
  right: 0;
  top: 20px;
  width: 120px;
  height: 154px;
}



