
.wrap {
  width: 100%;
  height: 340px;
  position: absolute;
  top: 50%;
  margin: -260px 0 0 0;
}
@media (min-width: 768px) {
.login-form {
    width: 580px;
    margin: 0 auto;
    height: 100%;
    padding: 30px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
}
@media (max-width: 768px) {
.login-form {
    width: 100%;
    height: 100%;
    padding: 20px;
    font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
}
.row > div {
  margin-bottom: 20px;
}
.input-group {
  position: relative;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: table;
  border-collapse: separate;
}
.input-group-addon:first-child {
  border-right: 0;
}
.input-group-addon {
  display: table-cell;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-collapse: separate;
  background-color: transparent;
  border-radius: 6px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  padding: 6px 12px;
  font-size: 14px;
  font-weight: normal;
  line-height: 1;
  text-align: center;
  border: 1px solid #ccc;
  width: 1%;
  white-space: nowrap;
  vertical-align: middle;
}
.login-form-control {
  display: table-cell;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border-collapse: separate;
  height: 60px;
  padding: 10px 30px 10px 0;
  color: #000;
  width: 100%;
  margin-bottom: 0;
  vertical-align: middle;
  background-color: #fff;
  border: 1px solid #ccc;
  border-left: none;
  border-radius: 6px;
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
  font-size: 18px;
  line-height: 1.33;
  background-image: none;
}
.login-form-control:focus {
  outline: none;
}
.login-form .icon {
  width: 18px;
  height: 18px;
  background-size: contain;
  background-repeat: no-repeat;
  margin: 0 8px;
}
.login-form .icon-user {
  background-image: url(../assets/user.png);
}
.login-form .icon-pwd {
  background-image: url(../assets/pwd.png);
}
.login-form .icon-clear {
  position: absolute;
  right: 10px;
  top: 20px;
  cursor: pointer;
  background-image: url(../assets/clear.png);
}
.login-form .icon-clear:hover {
  background-image: url(../assets/clear_hover.png);
}
.login-btn {
  width: 100%;
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #486a9a;
  border: none;
  color: #ffffff;
  -webkit-appearance: none;
  text-align: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  padding: 14px 15px;
  font-size: 18px;
  border-radius: 6px;
}
.lang-select {
  position: absolute;
  top: 10px;
  right: 20px;
  text-align: right;
  cursor: pointer;
  font-size: 16px;
  color: #666666;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
}
.lang-select:hover {
    color: #20a0ff;
}
.c-lang {
  color: #000000;
  font-size: 24px;
}
.login-failed {
  position: absolute;
  width: 100%;
  font-size: 24px;
  text-align: center;
  top: 20px;
  color: #ad2121;
  display: none;
  opacity: 1;
}
.trans {
  display: block;
  -webkit-transition: opacity ease-out 2s;
  transition: opacity ease-out 2s;
}
.fadeout {
  opacity: 0;
}



