
html {
  height: 100%;
}
#app {
  height: 100%;
}
body {
  -webkit-font-smoothing: antialiased;
  background-color: #ffffff;
  height: 100%;
}
.clearfix:before, .clearfix:after {
  content: " ";
  display: table;
}
.clearfix:after {
  clear: both;
}
.ft {
  position: fixed;
  width: 100%;
  left: 0;
  z-index: 999;
  bottom: 0;
  padding: 10px 0 20px 0;
  background: #ffffff;
  text-align: right;
}
.ft button {
    margin-right: 10px;
}
.fl {
  float: left;
}
.ps-wrap {
  position: fixed;
  z-index: 99999;
  display: block;
  top: 0px;
  left: 0px;
  background-color: #000;
  text-align: center;
  width: 100%;
  height: 100%;
  line-height: 974px;
}



