
.bay-detail {
  background-color: #ffffff;
  min-width: 860px;
  padding-bottom: 70px;
}
.bd-bg {
  position: absolute;
  right: 15px;
  top: 0;
  width: 330px;
  height: 100%;
  border: 1px solid #000000;
  background: -webkit-gradient(linear, left top, left bottom, from(#a6a6a6), to(#d6d6d6));
  background: linear-gradient(to bottom, #a6a6a6, #d6d6d6);
  z-index: 1;
}
.bd-bg div {
    float: left;
    padding-left: 30px;
    padding-top: 5px;
}



