
.tanker-detail {
  width: 980px;
  margin: 0 auto;
  background-color: #ffffff;
  padding-bottom: 66px;
}
.cmpt {
  clear: both;
  width: 940px;
  background-image: url(../assets/Tanker.png);
  background-repeat: no-repeat;
  position: relative;
  margin: 0 auto;
  margin-top: 40px;
  height: 420px;
}
.arm-wrap {
  width: 100%;
  position: absolute;
  z-index: 2;
  height: 210px;
  top: 195px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.arm-wrap .armth {
    position: absolute;
    z-index: 3;
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#f0f0f0));
    background: linear-gradient(to bottom, #ffffff, #f0f0f0);
    top: 152px;
    left: 240px;
}
.arm-wrap .armth div {
      padding: 3px 4px;
}
.arm-wrap .allarm {
    position: absolute;
    width: 504px;
    left: 366px;
    overflow: hidden;
}
.arm-wrap .arm-box {
    width: 100%;
    position: absolute;
    height: 60px;
    z-index: 2;
    top: 150px;
    border: 1px solid #000000;
    background: -webkit-gradient(linear, left top, left bottom, from(#ffffff), to(#f0f0f0));
    background: linear-gradient(to bottom, #ffffff, #f0f0f0);
}
.cmpt-container {
  width: 504px;
  position: absolute;
  top: 86px;
  left: 366px;
  z-index: 2;
  overflow: hidden;
}
.arm-container {
  width: 504px;
  z-index: 3;
  position: absolute;
  left: 366px;
  overflow: hidden;
}
.arm-c-f > div {
  width: 84px;
  float: left;
}
.arm-c-f > div > div.armv {
    padding: 4px;
    text-align: center;
}
.cmptth {
  width: 126px;
  position: absolute;
  top: 86px;
  left: 240px;
  z-index: 3;
  border: 1px solid #fff;
  border-top: none;
}
.cmptth div {
    white-space: nowrap;
    height: 26px;
    line-height: 26px;
    padding: 0 2px;
    border-top: 1px solid #fff;
}
.c-right {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
@media (max-width: 768px) {
.load-detail {
    margin: 0 auto;
    margin-top: 15px;
    float: none !important;
}
.load-status {
    margin: 0 auto;
}
}
@-webkit-keyframes armload {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
}
}
@keyframes armload {
from {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}
to {
    -webkit-transform: translateY(-40px);
            transform: translateY(-40px);
}
}
.hg {
  height: 150px;
  overflow: hidden;
}
.hg div {
    width: 16px;
    border: 2px solid #666;
    border-top: none;
    border-bottom: none;
    margin: 0 auto;
    height: 200px;
    background-color: #ccc;
}
.hg .arm-load {
    background-image: url(../assets/ts.png);
    -webkit-animation: armload 1s linear infinite;
            animation: armload 1s linear infinite;
}
.maq-c {
  -webkit-animation: maq 2s linear infinite;
          animation: maq 2s linear infinite;
}



