
.cmpt-item {
  width: 84px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background: -webkit-gradient(linear, left bottom, left top, from(#979797), to(#a4a4a4));
  background: linear-gradient(to top, #979797, #a4a4a4);
  position: relative;
}
.cmpt-process {
  position: absolute;
  bottom: 0;
  width: 100%;
  z-index: 1;
}
.cmpt-data {
  border: 1px solid #fff;
  border-top: none;
  position: relative;
  z-index: 2;
}
.cmpt-data div {
    white-space: nowrap;
    overflow: hidden;
    height: 26px;
    line-height: 26px;
    padding: 0 2px;
    border-top: 1px solid #fff;
}



